@keyframes trans1 {
  from {
    transform: translateY(0%);
  }
  to {
    transform: translateY(100%);
  }
}
.loader-outer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: #fff;
  visibility: visible;
  opacity: 1;
  transition: visibility 0.5s, opacity 0.3s linear;
}
.loader-outer.hide {
  visibility: hidden;
  opacity: 0;
}
.loader-outer .loader-container {
  height: 100%;
  width: 100%;
  display: table;
}
.loader-outer .loader-inner {
  vertical-align: middle;
  height: 100%;
  display: table-cell;
}
.loader-outer .loader {
  width: 100px;
  margin: auto;
  position: relative;
  height: 100px;
  overflow: hidden;
}
.loader-outer .mask {
  position: absolute;
  overflow: hidden;
  border-radius: 5px;
  width: 100px;
  height: 7rem;
  left: 0px;
  top: 0px;
}
.loader-outer .mask:after {
  content: "";
  width: 100%;
  height: 100%;
  background: #fff;
  position: absolute;
  transform: translateY(0);
  animation: 2s linear 0.1s infinite backwards trans1;
}
.loader-outer .plane {
  width: 100%;
  height: 100%;
  position: absolute;
