.banner-slide .item {
  height: 80vh;
  max-height: 55rem;
}
.banner-slide .item a img {
  width: 10rem !important;
}
.banner-slide .bg-main {
  margin-top: 3.3rem;
  margin-bottom: 3.3rem;
  position: relative;
}
.banner-slide .bg-main::before {
  background: url("../images/redTopMain.svg") no-repeat center;
  bottom: 100%;
}
.banner-slide .bg-main::after {
  background: url("../images/redBotMain.svg") no-repeat center;
  top: 100%;
}
.banner-slide .bg-main::before, .banner-slide .bg-main::after {
  content: "";
  width: 100%;
  height: 3.3rem;
  background-size: cover;
  position: absolute;
  left: 0;
}
.banner-slide.owl-carousel .owl-dots {
  left: 90%;
  bottom: 11rem;
  z-index: 99;
  display: flex;
}
.banner-slide.owl-carousel .owl-dots.stop .owl-dot {
  background: #f3dc8d;
}
.banner-slide.owl-carousel .owl-dots.stop .owl-dot.active {
  width: 5px;
  background: #750000;
}
.banner-slide.owl-carousel .owl-dots:not(.stop) .owl-dot.active span {
  background: #750000;
  display: inline-block;
  height: 100%;
  width: 0%;
  position: absolute;
  top: 0;
  left: 0;
  animation: 5s linear 0.1s infinite backwards trans2;
}
.banner-slide.owl-carousel .owl-dot {
  background: #750000;
  border: none;
}
.banner-slide.owl-carousel .owl-dot.active {
  background: rgba(243, 220, 141, 0.568627451);
  border-radius: 3px;
  overflow: hidden;
  transition: width 0.38s ease-out, background 0.38s ease-out;
  width: 5rem;
  position: relative;
  border: none;
}

@keyframes trans2 {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}
.hero-control-play {
  bottom: 10.5rem;
