@media (max-width: 1024px) {
  .btn-open {
    display: inline-flex !important;
  }
  .btn-close {
    display: inline-block !important;
    height: max-content;
  }
  #showRightPush {
    height: 27px;
    width: 39px;
  }
  #showRightPush:before, #showRightPush:after,
  #showRightPush .line-b {
    position: absolute;
    content: "";
    width: 100%;
    height: 4px;
    background: #eed588;
    transition: 0.3s all linear;
    border-radius: 2px;
  }
  #showRightPush:before {
    top: 0;
  }
  #showRightPush .line-b {
    top: 11px;
  }
  #showRightPush:after {
    bottom: 0;
  }
  #showRightPush.active::before {
    transform: rotate(45deg) translate(10px, 10px);
  }
  #showRightPush.active::after {
    transform: rotate(-45deg) translate(6px, -6px);
  }
  #showRightPush.active .line-b {
    opacity: 0;
  }
  .menu-menu-main-container {
    max-height: 100vh;
    overflow-y: auto;
    width: 100%;
  }
  .menu-menu .menu {
    display: block;
    padding-top: 4rem;
  }
  .menu-menu .menu .menu-item {
    position: relative;
  }
  .menu-menu .menu .menu-item .cat-item.active h2 {
    text-decoration: underline;
  }
  .menu-menu .menu .menu-item > a {
    margin-bottom: 0 !important;
    padding: 1rem 0 !important;
    color: #750000 !important;
  }
  .menu-menu .menu .menu-item.active > a {
    text-decoration: underline;
  }
  .menu-menu .menu .menu-item .bold.fs-20.color-main {
    font-size: 1.8rem !important;
  }
  .menu-menu .menu > .menu-item > a {
    width: 90%;
    margin-bottom: 1rem;
  }
  .menu-menu .menu-active {
    width: 30rem;
    max-width: 80%;
    padding-left: 2rem;
    position: fixed;
    left: -100%;
    top: 0;
    bottom: 0;
    background-color: #eaeaea;
    box-shadow: 3px 4px 11px 0 #454242;
    transition: all 0.3s;
    z-index: 9999;
    align-items: flex-start !important;
    align-content: space-between;
  }
  .menu-menu .menu-active.show-menu-mb {
    left: 0;
    transition: all 0.3s;
